body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
}

.page-wrapper {
    display: flex;
    min-height: 100vh;
}

.main-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
}

.ad-space {
    width: 160px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f0f0f0;
}

.ad-container {
    width: 120px;
    height: 600px;
    background-color: #ddd;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #ccc;
}

.container {
    display: flex;
    width: 100%;
    max-width: 1200px;
    background: white;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 0 20px rgba(0,0,0,0.1);
}

.input-section, .output-section {
    flex: 1;
    padding: 20px;
    display: flex;
    flex-direction: column;
}

h2 {
    margin-top: 0;
    color: #333;
}

textarea {
    flex: 1;
    min-height: 500px;
    border: 1px solid #ddd;
    background: #f7f9fc;
    padding: 15px;
    border-radius: 5px;
    resize: none;
    font-family: monospace;
    font-size: 14px;
    line-height: 1.5;
}

.control-section {
    width: 80px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #4a90e2;
}

button {
    background: white;
    border: none;
    padding: 15px;
    margin: 10px;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 20px;
}

button:hover {
    transform: scale(1.1);
    box-shadow: 0 0 10px rgba(255,255,255,0.5);
}

@media (max-width: 1200px) {
    .ad-space {
        display: none;
    }
}

@media (max-width: 768px) {
    .container {
        flex-direction: column;
    }
    .control-section {
        width: 100%;
        flex-direction: row;
        height: 80px;
    }
    textarea {
        min-height: 300px;
    }
}